@import"https://fonts.googleapis.com/css?family=Montserrat:400,600,700";@import"https://fonts.googleapis.com/css?family=Raleway";@import"https://fonts.googleapis.com/css?family=Montserrat+Subrayada";@import"utils/cssreset.css";body{background:#fff;line-height:1.4em;padding:0px;margin:0px}a{text-decoration:none;color:inherit}li{list-style-type:none;margin-bottom:1.5em}.App{text-align:left;font-family:"Raleway";color:#383838}.NavBar{font-family:"Montserrat";text-align:center;padding:20px;-webkit-box-shadow:0 5px 5px rgba(0,0,0,.16);box-shadow:0 5px 5px rgba(0,0,0,.16);background-color:#fff;position:relative;z-index:9999}.NavBar .NavOption{vertical-align:middle}.MobileNav{text-align:left}.MobileNav img{display:inline-block;vertical-align:middle;margin-right:1em;width:30px;cursor:pointer}.MobileNav .MobileNavLinks{margin-left:calc(30px + 1em)}.MobileNav .Open{-webkit-animation:dropdown 300ms ease-in-out forwards;animation:dropdown 300ms ease-in-out forwards;-webkit-transform-origin:top center;-ms-transform-origin:top center;transform-origin:top center}#Home{font-family:"Montserrat";color:#a64521}.NavOption{margin-right:48px;font-size:24px;cursor:pointer;text-transform:uppercase;color:#1e4959}.NavOption.active{color:#518294}.StudentList{margin-top:2em;margin-bottom:1em;font-size:1.5em;border-bottom:1px solid #000}.ListPage{margin-top:30px}.Home h1,.Home h2{margin-bottom:.5em}.Home h1{font-size:1.8em}.Home h2{font-size:1.5em}.Caption{background-color:rgba(0,0,0,.6);width:-webkit-max-content;width:-moz-max-content;width:max-content;position:fixed;bottom:0;right:0;color:#fff;padding:5px;display:none}.Statement{line-height:1.4em}.PubCard{width:200px;margin-right:10px;padding:20px;background-color:#f2d8c2;border-bottom:3px solid #bf6836;opacity:75%}.PubCard a{color:#bf6836;font-size:18px}.PubCard a:hover{text-decoration:underline}.PubCard p{margin-top:10px;padding:5px;background-color:#bf6836;color:#f2d8c2;width:-webkit-max-content;width:-moz-max-content;width:max-content}.Group{margin-bottom:20px}.Group a{color:#a64521}.Group .GroupName{font-size:24px}@media only screen and (min-width: 769px){.Home{background-size:100%;background-repeat:no-repeat;background-position:bottom;background-blend-mode:soft-light;height:150vh;padding:15px;z-index:-9999}.Home .Statement{padding-left:3em;max-width:40vw}.HomePubList{display:-ms-flexbox;display:flex}.Caption{display:inherit}.Statement,.WhatsNew{margin-left:150px;margin-top:30px}.TopicNav{margin-left:50px;position:absolute;height:100%;width:200px;float:left}.NavOption{font-size:1.7vw}}@media only screen and (max-width: 769px){.Statement,.WhatsNew{margin:50px}.PubCard{margin-bottom:20px}.TopicNav{padding:20px;display:grid;grid-template-columns:repeat(2, 1fr)}}@-webkit-keyframes dropdown{from{-webkit-transform:translateY(-50px);transform:translateY(-50px)}to{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes dropdown{from{-webkit-transform:translateY(-50px);transform:translateY(-50px)}to{-webkit-transform:translateY(0px);transform:translateY(0px)}}


